.wordle-title {
	font-weight: 700;
	font-size: 36px;
	letter-spacing: 0.2rem;
	text-transform: uppercase;
	text-align: center;
	position: absolute;
	left: 0;
	right: 0;
	pointer-events: none;
	font-family: 'Clear Sans', 'Helvetica Neue', Arial, sans-serif;
}

.wordle-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 50px;
	color: #1a1a1b;
	border-bottom: 1px solid #d3d6da;
	width: 500px;
	margin-bottom: 30px;
}

.wordle-game-container {
	width: fit-content;
	margin: 0 auto;
}

.wordle-restart {
	background-color: #d3d6da;
	border: 0;
	border-radius: 4px;
	width: 200px;
	height: 50px;
	font-family: 'Clear Sans', 'Helvetica Neue', Arial, sans-serif;
	font-size: 18px;
	line-height: 18px;
	letter-spacing: 0.1rem;
	font-weight: 700;
	cursor: pointer;
	display: inline-flex;
	text-align: center;
	justify-content: center;
	vertical-align: middle;
	align-items: center;
}
