.wordle-cell {
	margin-left: 5px;
	margin-right: 5px;
	height: 62px;
	line-height: 32px;
	font-size: 32px;
	font-weight: 700;
	width: 62px;
	display: inline-flex;
	text-align: center;
	justify-content: center;
	vertical-align: middle;
	align-items: center;
	font-family: Arial, Helvetica, sans-serif;
	user-select: none;
}

.wordle-empty-cell {
	border: 2px solid #d3d6da;
}

.wordle-unsubmitted-cell {
	border: 2px solid #878a8c;
}

.wordle-wrong-cell {
	color: white;
	border: 2px solid #787c7e;
	background-color: #787c7e;
}

.wordle-misplaced-cell {
	color: white;
	border: 2px solid #c9b458;
	background-color: #c9b458;
}

.wordle-correct-cell {
	color: white;
	border: 2px solid #6aaa64;
	background-color: #6aaa64;
}
